/*eslint-disable*/module.exports={messages:{"- Ein Anspruch auf **Aufwendungsersatz** ist das Recht auf **Erstattung der Kosten**, die bei der **Beseitigung des Mangels** entstanden sind.\n- Sie dürfen den Mangel selbst beseitigen, wenn die Vermieterin **mit der Beseitigung des Mangels in Verzug** ist, oder es sich um eine **Not- oder Eilmaßnahme** handelt.\n- Meistens sollten Sie den Mangel erst selbst beseitigen, wenn Sie Ihre Vermieterin mit einer Frist zur Beseitigung aufgefordert haben, und der Mangel danach nicht behoben wurde.":"- Ein Anspruch auf **Aufwendungsersatz** ist das Recht auf **Erstattung der Kosten**, die bei der **Beseitigung des Mangels** entstanden sind.\n- Sie dürfen den Mangel selbst beseitigen, wenn die Vermieterin **mit der Beseitigung des Mangels in Verzug** ist, oder es sich um eine **Not- oder Eilmaßnahme** handelt.\n- Meistens sollten Sie den Mangel erst selbst beseitigen, wenn Sie Ihre Vermieterin mit einer Frist zur Beseitigung aufgefordert haben, und der Mangel danach nicht behoben wurde.","- Ein Anspruch auf **Ausgleichszahlung** ist das Recht auf eine **Entschädigung** durch Ihre Fluglinie.\n- Sie bekommen eine Ausgleichszahlung, wenn Sie Ihr Ziel wegen **Verspätung**, **Annullierung** oder **Überbuchung** nicht oder nur verspätet erreichen konnten.\n- Die **Höhe der Zahlung** richtet sich nach der **Flugstrecke** und beträgt **250€ (unter 1.500km)**, **400€ (zwischen 1.500km und 3.500km)** oder **600€ (über 3.500km)**.":"- Ein Anspruch auf **Ausgleichszahlung** ist das Recht auf eine **Entschädigung** durch Ihre Fluglinie.\n- Sie bekommen eine Ausgleichszahlung, wenn Sie Ihr Ziel wegen **Verspätung**, **Annullierung** oder **Überbuchung** nicht oder nur verspätet erreichen konnten.\n- Die **Höhe der Zahlung** richtet sich nach der **Flugstrecke** und beträgt **250€ (unter 1.500km)**, **400€ (zwischen 1.500km und 3.500km)** oder **600€ (über 3.500km)**.","- Ein Anspruch auf **Betreuungsleistungen** ist das Recht auf zusätzliche Unterstützung wie Essen und Getränke, wenn sich Ihr **Abflug stark verzögert**.\n- Betreuungsleistungen müssen **angemessen zur Wartezeit und Tageszeit** sein. (Kleine) Mahlzeiten und Getränke sowie Zugang zu  Telefon oder Internet sind immer angemessen.\n- Wenn **lange Wartezeiten am Flughafen bei Nacht** entstehen, kann auch das Recht auf eine **Hotelunterbringung** mit Transport dorthin bestehen. \n- In der Regel werden Ihnen diese Leistungen **von der Fluglinie angeboten**; wenn nicht, können Sie **Schadensersatz** für notwendige Ausgaben mit Rechnungen beantragen.":"- Ein Anspruch auf **Betreuungsleistungen** ist das Recht auf zusätzliche Unterstützung wie Essen und Getränke, wenn sich Ihr **Abflug stark verzögert**.\n- Betreuungsleistungen müssen **angemessen zur Wartezeit und Tageszeit** sein. (Kleine) Mahlzeiten und Getränke sowie Zugang zu  Telefon oder Internet sind immer angemessen.\n- Wenn **lange Wartezeiten am Flughafen bei Nacht** entstehen, kann auch das Recht auf eine **Hotelunterbringung** mit Transport dorthin bestehen. \n- In der Regel werden Ihnen diese Leistungen **von der Fluglinie angeboten**; wenn nicht, können Sie **Schadensersatz** für notwendige Ausgaben mit Rechnungen beantragen.","- Ein Anspruch auf **Mietminderung** ist das Recht, eine **geringere Gesamtmiete zu zahlen**, wenn die **Nutzung der Wohnung**\ndurch einen Mangel **erheblich eingeschränkt** ist. \n- Ein **erheblicher Mangel** liegt vor, wenn der Mangel nicht nur optisch und die Wohnqualität stark beeinträchtigt ist.\n- Der Zeitraum der Minderung **beginnt mit der Anzeige des Mangels** bei der Vermieter:in, und **endet mit der Beseitigung**.\n- Sie sollten nicht sofort eine geringere Miete zahlen, da Sie so eine Kündigung wegen Mietverzug riskieren können. Stattdessen können Sie Ihrer Vermieterin melden, die **Miete unter Vorbehalt zu zahlen**. Nach Beseitigung des Mangels können Sie dann einen Anteil zurückfordern.\n- Um **wie viel Prozent** die Gesamtmiete gekürzt werden kann, hängt von der **Schwere des Mangels** ab. Einen Anhaltspunkt für Ihren Fall können Sie in einer **Mietminderungstabelle** im Internet finden.":"- Ein Anspruch auf **Mietminderung** ist das Recht, eine **geringere Gesamtmiete zu zahlen**, wenn die **Nutzung der Wohnung**\ndurch einen Mangel **erheblich eingeschränkt** ist. \n- Ein **erheblicher Mangel** liegt vor, wenn der Mangel nicht nur optisch und die Wohnqualität stark beeinträchtigt ist.\n- Der Zeitraum der Minderung **beginnt mit der Anzeige des Mangels** bei der Vermieter:in, und **endet mit der Beseitigung**.\n- Sie sollten nicht sofort eine geringere Miete zahlen, da Sie so eine Kündigung wegen Mietverzug riskieren können. Stattdessen können Sie Ihrer Vermieterin melden, die **Miete unter Vorbehalt zu zahlen**. Nach Beseitigung des Mangels können Sie dann einen Anteil zurückfordern.\n- Um **wie viel Prozent** die Gesamtmiete gekürzt werden kann, hängt von der **Schwere des Mangels** ab. Einen Anhaltspunkt für Ihren Fall können Sie in einer **Mietminderungstabelle** im Internet finden.","- Ein Anspruch auf **Mängelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu übergeben und zu erhalten.\n- Wenn während der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.\n- Ausnahme: Mietende können für Kleinreperaturen bis etwa 100 Euro veranwortlich sein, wenn das im Mietvertrag festgelegt wurde.":"- Ein Anspruch auf **Mängelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu übergeben und zu erhalten.\n- Wenn während der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.\n- Ausnahme: Mietende können für Kleinreperaturen bis etwa 100 Euro veranwortlich sein, wenn das im Mietvertrag festgelegt wurde.","- Ein Anspruch auf **Rückerstattung der Flugscheinkosten** ist das Recht, Ihren gezahlten Ticketpreis bei Annullierung innerhalb einer Woche zurückzuerhalten.\n- Zusätzlich erhalten Sie einen Rückflug zu Ihrem ersten Abflugort, wenn ein Anschlussflug annuliert wurde.\n- Statt der Rückerstattung können Sie auch eine anderweitige Beförderung zu Ihrem Zielort durch die Fluglinie wählen.":"- Ein Anspruch auf **Rückerstattung der Flugscheinkosten** ist das Recht, Ihren gezahlten Ticketpreis bei Annullierung innerhalb einer Woche zurückzuerhalten.\n- Zusätzlich erhalten Sie einen Rückflug zu Ihrem ersten Abflugort, wenn ein Anschlussflug annuliert wurde.\n- Statt der Rückerstattung können Sie auch eine anderweitige Beförderung zu Ihrem Zielort durch die Fluglinie wählen.","- Ein Anspruch auf **Schadensersatz** ist das Recht, sich **Geld** für durch den Mangel **entstandene Schäden erstatten** zu lassen.\n- **Mögliche Schäden** sind entstandene **Hotelkosten**, Wertverlust bei unbrauchbar gewordenen **Möbeln**, oder auch Schmerzensgeld bei körperlichen Schäden.\n- Wichtig ist, dass die Vermieter:in den Mangel zu vertreten hat, mit der Beseitigung in Verzug ist, oder diesen bei Vertragsabschluss verschwiegen hat.":"- Ein Anspruch auf **Schadensersatz** ist das Recht, sich **Geld** für durch den Mangel **entstandene Schäden erstatten** zu lassen.\n- **Mögliche Schäden** sind entstandene **Hotelkosten**, Wertverlust bei unbrauchbar gewordenen **Möbeln**, oder auch Schmerzensgeld bei körperlichen Schäden.\n- Wichtig ist, dass die Vermieter:in den Mangel zu vertreten hat, mit der Beseitigung in Verzug ist, oder diesen bei Vertragsabschluss verschwiegen hat.","Aufwendungsersatz":"Aufwendungsersatz","Ausgleichszahlung":"Ausgleichszahlung","Außergerichtliche Einigung":"Außergerichtliche Einigung","Beseitigung des Mangels durch die Vermieter:in":"Видалення дефекту орендодавцім","Betreuungsleistungen":"Betreuungsleistungen","Die Justiz im Netz.":"Судова влада в мережі.","Diese Option ist leider noch nicht verfügbar.":"На жаль, ця опція поки недоступна.","Dokumentation des Mangels":"Dokumentation des Mangels","Einfach, online, jederzeit.":"Легко, онлайн, в будь-який час.","Erstberatung":"Erstberatung","Fall Sie sich unsicher fühlen, können Sie sich auch beraten lassen. Dafür haben wir <0>hier Beratungsangebote zusammengefasst.</0>":"Fall Sie sich unsicher fühlen, können Sie sich auch beraten lassen. Dafür haben wir <0>hier Beratungsangebote zusammengefasst.</0>","Fluglinie kontaktieren":"Fluglinie kontaktieren","Gerichtsverfahren":"Gerichtsverfahren","Handlungsoptionen vergleichen":"Порівняння параметрів дії","In welchem Bereich Ihres Lebens haben Sie ein Problem?":"В якій області вашого життя ви маєте проблеми?","Info":"інфо","Ja":"так","Jetzt starten!":"Почніть зараз!","Lösungsfinder":"Рішення шукач","Mehr erfahren":"Mehr erfahren","Mietminderung":"зниження орендної плати","Mängelanzeige bei Ihrer Vermieter:in":"Mängelanzeige bei Ihrer Vermieter:in","Mögliche Ansprüche":"Mögliche Ansprüche","Mögliche Lösungen":"Mögliche Lösungen","Nein":"ні","Nur kostenlose Angebote anzeigen":"Nur kostenlose Angebote anzeigen","Nächste Schritte":"Nächste Schritte","Online Klage einreichen":"Посуд подати онлайн","Optionen":"Optionen","Problem einordnen":"Problem einordnen","Rechtlich beraten lassen":"Rechtlich beraten lassen","Rechtsproblem einordnen":"Класифікувати юридичну проблему","Rückerstattung der Flugscheinkosten":"Rückerstattung der Flugscheinkosten","Schadensersatz":"Schadensersatz","Schlichtungsantrag stellen":"Schlichtungsantrag stellen","Schritt":"Schritt","Startseite":"Startseite","Unser Angebot":"Наша пропозиція","Vermieter:in erneut kontaktieren":"Vermieter:in erneut kontaktieren","Wie es funktioniert":"Як це працює","Wobei können wir Ihnen helfen?":"Як ми можемо вам допомогти?","ZPO Informationen":"ZPO Informationen","Zum Prototypen":"Zum Prototypen","Zurück":"назад","Zurück zur Homepage":"Zurück zur Homepage","back":"Zurück","consultation.header":"Beratungsangebote","consultation.sub_header":"Örtliche und überregionale Beratungsstellen können Sie persönlich und individuell bei Ihrem Fall unterstützen. Hier haben wir verschiedene Angebote aufgelistet. Wobei benötigen Sie Unterstützung?","exit.footer":"Sie suchen noch weitere Unterstützung? Dann können Sie sich an rechtliche Beratungsstellen wenden, die Sie individuell zu Ihrem Fall informieren können. In einer Beratung beurteilen Expert:innen, ob rechtliche Ansprüche bestehen und wie Sie diese am besten durchsetzen können. Dafür haben wir [hier einen Überblick über Beratungsstellen aufbereitet](/beratungsangebote).","exit.heading":"Leider haben wir Ihren Fall nicht abgedeckt.","forward":"Weiter","home.hero_offer":"Justizportal.de - пропозиція Федерального міністерства юстиції та захисту прав споживачів у співпраці з федеральними міністерствами юстиції у федеральних штатах. Портал дає вам огляд ваших прав в різних повсякденних ситуаціях. Це також допоможе вам класифікувати і вирішувати ваші юридичні питання і проблеми. Ви будете проінформовані про ваші варіанти дій і будуть підтримані у відстоюванні ваших прав.","home.howitwork_claims":"Потім ми допоможемо вам оцінити, які претензії можуть існувати у вашому випадку. Претензії - це права вимагати конкретних дій від інших (наприклад, відшкодування збитків), терпіти (наприклад, суборенда квартири) або утриматися від (наприклад, входження в власність). invidunt ut labore.","home.howitwork_classify":"По-перше, ми допоможемо вам уточнити, чи є юридична проблема у вашому випадку. Ми покажемо вам інформацію, адаптовану до вашого випадку.","home.howitwork_heading":"Як це працює","home.howitwork_lawsuit":"Коли всі інші варіанти вичерпані, ми допоможемо вам подати позов до районного суду онлайн. Для цього ми пояснюємо Вам процес нашого онлайн-процесу і надаємо прозору інформацію про витрати на судочинство, а також можливі юридичні консультації.","home.howitwork_options":"Виходячи з цього, ми запропонуємо для <0>вас можливі рішення - спочатку для мирового вирішення спорів з протилежного боку.</0>","home.howitwork_subheading":"Ми допоможемо вам вирішити вашу юридичну проблему в чотири простих кроки:","home.howtohelp_footer":"Ви можете дізнатися більше про юридичні проблеми, які ми можемо вам допомогти на наступних сторінках.","home.sub_heading":"Ми інформуємо вас про ваші права в повсякденному житті і допоможемо вирішити вашу юридичну проблему простими кроками","nextsteps.header":"Ende des Lösungfinders: Mögliche nächste Schritte","nextsteps.sub_header":"Hier haben wir für Sie nächste Schritte zusammengefasst, um Ihre Ansprüche durchzusetzen. <0/>Klicken Sie nacheinander auf die Felder, um mehr darüber zu erfahren.","pe.header":"Mögliche rechtliche Ansprüche","pe.sub_header":"Aus Ihren Angaben könnten sich die folgenden Ansprüche ergeben. Ein Anspruch ist das Recht, etwas von einer anderen Person oder einem Unternehmen zu fordern. Dabei können Sie auch mehrere Ansprüche gleichzeitig durchsetzen. \nKlicken Sie auf die Ansprüche, um mehr darüber zu erfahren. Auf der nächsten Seite informieren wir Sie dann über mögliche nächste Schritte.","pe.to_next_steps":"Zu den nächsten Schritten","zpo.nextsteps":"1. Auf der kommenden Seite können Sie das Dokument für eine Klage zu Ihrem Fall erstellen.  \n2. Dabei schätzen wir die Gerichtskosten für Sie ab und informieren Sie über die nötigen nächsten Schritte.  \n3. Nach der Erstellung können Sie alle Informationen zunächst überprüfen. **Über die Einreichung entscheiden Sie erst dann.**","Über Ansprüche informieren":"Повідомляти про претензії"}};